function copy(ob) {
  var obj = findObj(ob);

  if (obj) {
    obj.select();
    js = obj.createTextRange();
    js.execCommand("Copy");
  }
}

function paste(ob) {
  var obj = findObj(ob);

  if (obj) {
    obj.select();
    js = obj.createTextRange();
    js.execCommand("Paste");
  }
}

function cut(ob) {
  var obj = findObj(ob);

  if (obj) {
    obj.select();
    js = obj.createTextRange();
    js.execCommand("Cut");
  }
}

function findObj(n, d) {
  //v4.0
  var p, i, x;
  if (!d) d = document;

  if ((p = n.indexOf("?")) > 0 && parent.frames.length) {
    d = parent.frames[n.substring(p + 1)].document;
    n = n.substring(0, p);
  }

  if (!(x = d[n]) && d.all) x = d.all[n];

  for (i = 0; !x && i < d.forms.length; i++) {
    x = d.forms[i][n];
  }

  for (i = 0; !x && d.layers && i < d.layers.length; i++) {
    x = findObj(n, d.layers[i].document);
  }

  if (!x && document.getElementById) x = document.getElementById(n);
  return x;
}

function charPYStr() {
  return '';
}

function ftPYStr() {
  return '';
}

exports.traditional = function (cc) {
  var str = '';

  if (!cc) {
    return '';
  }

  for (var i = 0; i < cc.length; i++) {
    if (charPYStr().indexOf(cc.charAt(i)) != -1) str += ftPYStr().charAt(charPYStr().indexOf(cc.charAt(i)));else str += cc.charAt(i);
  }

  return str;
};

exports.simplified = function (cc) {
  if (cc === undefined || cc === null) {
    return;
  }

  var str = '';

  for (var i = 0; i < cc.length; i++) {
    if (ftPYStr().indexOf(cc.charAt(i)) != -1) str += charPYStr().charAt(ftPYStr().indexOf(cc.charAt(i)));else str += cc.charAt(i);
  }

  return str;
};

function convert(nOption) {
  if (nOption == 0) txt.value = simplized(txt.value);else txt.value = traditionalized(txt.value);
}